.gtkxfc0 {
  position: relative;
  top: 5%;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 11;
}
.gtkxfc0:focus {
  outline: none;
}
.gtkxfc0 div {
  width: 2rem;
  height: 0.25rem;
  border-radius: 10px;
  transition: all 0.3s linear;
  position: relative;
  transform-origin: 1px;
}
.gtkxfc0.dark div {
  background: #020617;
}
.gtkxfc0.light div {
  background: #fff;
}
.gtkxfc0 div.open {
  background: #EFFFFA;
}
.gtkxfc0 div:first-child {
  transform: rotate(0);
}
.gtkxfc0.open div:first-child {
  transform: rotate(45deg);
}
.gtkxfc0 div:nth-child(2) {
  transform: translateX(0);
  opacity: 1;
}
.gtkxfc0.open div:nth-child(2) {
  opacity: 0;
  transform: translateXS(20px);
}
.gtkxfc0 div:nth-child(3) {
  transform: rotate(0);
}
.gtkxfc0.open div:nth-child(3) {
  transform: rotate(-45deg);
}